.comment {
  background-color: white;
  margin-bottom: 1rem;
  border-radius: 0.5rem;
  padding: 1rem;

  &__content {
    display: flex;
    flex-direction: column;
    p {
      color: var(--grayish-blue);
      span {
        color: var(--moderate-blue);
        font-weight: 500;
      }
    }
    &__textarea {
      box-sizing: border-box;
      margin: 1rem 0;
      font-family: "Rubik", sans-serif;
      padding: 1rem;
      width: 100%;
      font-size: 1rem;
      border: 1px solid var(--light-gray);
      resize: none;
      border-radius: 0.5rem;
      cursor: pointer;
    }
    &__textarea:focus {
      outline: 1px solid var(--moderate-blue);
    }
    button {
      align-self: flex-end;
      cursor: pointer;
      font-family: "Rubik", sans-serif;
      font-size: 1rem;
      padding: 0.75rem 1.5rem;
      border-radius: 0.5rem;
      border: none;
      background-color: var(--moderate-blue);
      color: white;
      text-transform: uppercase;
    }
    button:hover {
      opacity: 0.7;
    }
  }
  &__bottombar {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__score {
      background-color: var(--light-gray);
      padding: 0.25rem;
      border-radius: 0.5rem;
      display: flex;
      align-items: center;

      button {
        padding: 0.5rem;
        border: none;
        display: grid;
        place-content: center;
        background-color: transparent;
        cursor: pointer;
      }
      span {
        padding: 0 0.25rem;
        font-weight: 500;
        color: var(--moderate-blue);
      }
    }
    &__options {
      position: relative;
      display: flex;
      &__button {
        transition: var(--transition-time);
        cursor: pointer;
        padding: 0;
        display: flex;
        align-items: center;
        border: none;
        background-color: transparent;
        font-size: 1rem;
        margin-right: 1rem;

        &__icon {
          display: grid;
          place-content: center;
        }

        &__red {
          color: var(--soft-red) !important;
        }

        span {
          font-family: "Rubik", sans-serif;
          margin-left: 0.5rem;
          font-weight: 500;
          color: var(--moderate-blue);
        }
      }
      &__button:last-child {
        margin-right: 0;
      }
      &__button:hover {
        opacity: 0.7;
      }
    }
  }

  &__topbar {
    display: flex;
    align-items: center;

    &__avatar {
      width: 2rem;
      height: 2rem;
      margin-right: 1rem;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &__username {
      color: var(--dark-blue);
      font-weight: 500;
      margin-right: 1rem;
      display: flex;
      align-items: center;
      &__isAuthor {
        margin-left: 0.5rem;
        background-color: var(--moderate-blue);
        color: white;
        font-size: 0.8rem;
        font-weight: 300;
        padding: 0.1rem 0.3rem;
        border-radius: 0.2rem;
      }
    }
    &__datetime {
      color: var(--grayish-blue);
    }
  }
}
@media screen and (min-width: 768px) {
  .comment {
    padding: 2rem 2rem 2rem 7rem;
    position: relative;

    &__bottombar {
      &__score {
        position: absolute;
        left: 2rem;
        top: 2rem;
        flex-direction: column;

        button {
          padding: 0.75rem;
        }
        span {
          padding: 0.5rem 0;
        }
      }
      &__options {
        position: absolute;
        right: 2rem;
        top: 2rem;
      }
    }
  }
}
