.modal {
  background-color: rgba(0, 0, 0, 0.4);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  box-sizing: border-box;
  padding: 1rem;

  &__container {
    position: relative;
    font-family: "Rubik", sans-serif;
    padding: 1.5rem;
    background-color: white;
    border-radius: 0.5rem;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    max-width: 330px;

    h2 {
      color: var(--dark-blue);
      font-weight: 500;
      font-size: 1.25rem;
      margin: 0;
      padding: 0;
    }
    p {
      color: var(--grayish-blue);
    }
    &__buttons {
      display: flex;

      button {
        cursor: pointer;
        font-family: "Rubik", sans-serif;
        width: 100%;
        margin-right: 1rem;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 1rem;
        color: white;
        padding: 1rem;
        background-color: var(--grayish-blue);
        border: none;
        border-radius: 0.5rem;
      }
      button:last-child {
        margin-right: 0;
      }
      &__red {
        background-color: var(--soft-red) !important;
      }
    }
  }
}
