.comments__form__comment {
  width: 100%;
  padding: 1rem;
  background-color: white;
  box-sizing: border-box;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  &__textarea {
    cursor: pointer;
    border-radius: 0.5rem;
    font-family: "Rubik", sans-serif;
    font-size: 1rem;
    border: 1px solid var(--light-gray);
    box-sizing: border-box;
    padding: 1rem;
    width: 100%;
    resize: none;
  }
  &__textarea:focus {
    outline: 1px solid var(--moderate-blue);
  }
  &__bottombar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__avatar {
      width: 2rem;
      height: 2rem;
      margin-right: 1rem;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    button {
      cursor: pointer;
      font-family: "Rubik", sans-serif;
      font-size: 1rem;
      margin-top: 0.5rem;
      padding: 0.75rem 1.5rem;
      border-radius: 0.5rem;
      border: none;
      background-color: var(--moderate-blue);
      color: white;
      text-transform: uppercase;
    }
    button:hover {
      opacity: 0.7;
    }
  }
}

@media screen and (min-width: 768px) {
  .comments__form__comment {
    padding: 2rem 10rem 2rem 7rem;
    position: relative;

    &__bottombar {
      &__avatar {
        position: absolute;
        left: 2rem;
        top: 2rem;
        width: 3rem;
        height: 3rem;
      }
      button {
        position: absolute;
        right: 2rem;
        top: 2rem;
        margin-top: 0;
        padding: 0.75rem 2rem;
      }
    }
  }
}
