.comment__section {
  &__replies {
    border-left: 2px solid var(--light-gray);
    padding-left: 1rem;
  }
}
@media screen and (min-width: 768px) {
  .comment__section {
    &__replies {
      border-left: 2px solid var(--light-gray);
      padding-left: calc(3.5rem - 1px);
      margin-left: calc(3.5rem - 1px);
    }
  }
}
